.scrollbar-none::-webkit-scrollbar {
    display: none;
}

/* Default Light Theme */
.light {
    background-color: white;
    color: black;
}

/* Dark Theme */
.dark {
    background-color: #121212;
    color: white;
}

/* Light Theme */
.light {
    @apply bg-white text-black;
    /* Background and text color */
}

.light header {
    @apply bg-gray-200 bg-opacity-75;
    /* Header styles */
}

.light button {
    @apply bg-gray-300 text-black hover:bg-gray-700;
    /* Button styles */
}

.light input {
    @apply bg-white text-black border-gray-700;
    /* Input styles */
}

.light .divider {
    @apply border-gray-700;
    /* Divider styles */
}

.light p, span {
    @apply text-neutral-900
}

.light span {
    @apply text-neutral-900
}

.light h2 {
    @apply text-black
}

.light .trend {
    color: #fff;
}

.light .title {
    color: #fff
}

.light .whiteText {
    color: #fff;
}

.light .transparent {
    background: transparent;
    color: black;
}

.light .svg {
    background: transparent;
}

.light footer {
    background-color: #fff;
}

.light ul li {
    @apply text-neutral-900
}

.light svg {
    @apply text-neutral-900
}

.light footer h3 {
    @apply text-neutral-900
}

/* Dark Theme */
/* . */
/* Header */
header {
    transition: background-color 0.3s ease;
    /* Smooth transition */
}

@media (min-width: 426px) {
    button.hidden {
        display: none;
    }
}

/* Other styles for header, buttons, etc. */